import { CacheType, IAction, IActionContext, IActionInput } from '@msdyn365-commerce/core-internal';
import { createObservableDataAction } from '../../create-data-action.vnext';

/**
 * Test app action
 */
export class ObservableErrorActionInput implements IActionInput {
    public dataCacheType = (): CacheType => 'application';
    public getCacheObjectType = () => 'ObservableError';
    public getCacheKey = () => `ObservableError`;
}

const createInput = () => {
    return new ObservableErrorActionInput();
};

const testAction = async (input: ObservableErrorActionInput, context: IActionContext): Promise<string> => {
    throw new Error('Observable Error');
};

export default createObservableDataAction({
    input: createInput,
    action: <IAction<string>>testAction
});
